@use "../abstract" as *;
.footer-section {
  .footer-top-shape {
    position: absolute;
    top: 30%;
    left: 30%;
    z-index: -1;
    width: 30%;
    img {
      width: 100%;
      height: auto;
      overflow: hidden;
      object-fit: contain;
    }
  }
  .footer-content {
    p {
      font-size: 14px;
      @media (min-width: 768px) {
        font-size: 16px;
      }
    }
    .widget-title {
      font-size: 24px;
    }
  }
  .btn {
    i {
      padding-left: 0;
      font-size: 16px;
      @media (min-width: 768px) {
        font-size: 19px;
      }
    }
  }
  .widget-list {
    li {
      margin-bottom: 10px;
      a {
        color: $heading-color;
        font-size: 16px;
        @media (min-width: 768px) {
          font-size: 19px;
        }
        transition: 0.3s;
        &:hover {
          color: $secondary;
        }
      }
    }
  }
  .footer-bottom {
    padding-top: 60px;
    position: relative;
    z-index: 2;
    @media (min-width: 767px) {
      padding-top: 80px;
    }
    @media (min-width: 1200px) {
      padding-top: 100px;
    }
    @media (min-width: 1600px) {
      padding-top: 350px;
    }
    .footer-bottom-pb {
      padding-bottom: 10px;
      @media (min-width: 1200px) {
        padding-bottom: 20px;
      }
      @media (min-width: 1600px) {
        padding-bottom: 30px;
      }
    }
  }
  .footer-bottom-shape {
    position: absolute;
    bottom: 0%;
    left: 0%;
    z-index: 0;
    width: 100%;
    img {
      width: 100%;
      height: auto;
      overflow: hidden;
      object-fit: contain;
    }
  }
  .footer-copyright {
    color: $white;
    &__links {
      a {
        color: $white;
        margin-left: 20px;
        font-size: 16px;
        transition: 0.3s;
        &:hover {
          color: $secondary;
        }
      }
    }
  }
}
